import { Vue3Marquee } from 'vue3-marquee';
import 'vue3-marquee/dist/style.css';
import { mapMutations, mapState } from 'vuex';
export default {
  data() {
    return {
      isLyricShow: false
    };
  },

  computed: { ...mapState(["lyricList", "currentTime", "playListIndex", "playList", "duration"]),
    // 对歌词的处理
    lyric: function () {
      let arr;

      if (this.lyricList.lyric) {
        arr = this.lyricList.lyric.split(/[(\r\n)\r\n]+/).map((item, i) => {
          let min = item.slice(1, 3);
          let sec = item.slice(4, 6);
          let mill = item.slice(7, 10);
          let lrc = item.slice(11, item.length);
          let time = parseInt(min) * 60 * 1000 + parseInt(sec) * 1000 + parseInt(mill);

          if (isNaN(Number(mill))) {
            mill = item.slice(7, 9);
            lrc = item.slice(10, item.length);
            let time = parseInt(min) * 60 * 1000 + parseInt(sec) * 1000 + parseInt(mill);
          }

          return {
            min,
            sec,
            mill,
            lrc,
            time
          };
        });
        arr.forEach((item, i) => {
          if (i === arr.length - 1) {
            item.pre = 0;
          } else {
            item.pre = arr[i + 1].time;
          }
        });
      }

      return arr;
    }
  },
  components: {
    Vue3Marquee
  },

  mounted() {
    // console.log(this.musicList);
    // console.log(this.lyricList.lyric);
    this.addDuration();
  },

  // 接收父组件FooterMusic的传值
  props: ['musicList', 'play', 'isbtnShow', 'addDuration'],
  methods: {
    backHome: function () {
      this.isLyricShow = false;
      this.updataDetailShow();
    },
    // 上下首切换
    goPlay: function (num) {
      let index = this.playListIndex + num;

      if (index < 0) {
        index = this.playList.length - 1;
      } else if (index == this.playList.length) {
        index = 0;
      }

      this.updataPlayListIndex(index);
    },
    ...mapMutations(['updataDetailShow', 'updataPlayListIndex'])
  },
  watch: {
    currentTime: function (newValue) {
      let p = document.querySelector("p.active");

      if (p && p.offsetTop > 300) {
        this.$refs.musicLyric.scrollTop = p.offsetTop - 300;
      }

      if (newValue === this.duration) {
        if (this.playListIndex === this.playList.length - 1) {
          this.updataPlayListIndex(0);
          this.play();
        } else {
          this.updataPlayListIndex(this.playListIndex);
        }
      }
    }
  }
};