import { getMusicList } from "@/request/api/home.js";
export default {
  data() {
    return {
      musicList: []
    };
  },

  methods: {
    async getGedan() {
      let res = await getMusicList();
      console.log(res);
      this.musicList = res.data.result;
    },

    // 对播放量格式进行处理的函数
    changeCount: function (num) {
      if (num >= 100000000) {
        return (num / 10000000).toFixed(1) + "亿";
      } else if (num >= 10000) {
        return (num / 10000).toFixed(1) + "万";
      }
    }
  },

  mounted() {
    this.getGedan();
  }

};