import { mapState, mapMutations } from 'vuex';
import MusicDetail from './MusicDetail.vue';
export default {
  data() {
    return {
      interVal: 0
    };
  },

  components: {
    MusicDetail
  },
  computed: { ...mapState(['playList', 'playListIndex', 'isbtnShow', 'detailShow'])
  },

  mounted() {
    console.log(this.$refs);
    this.$store.dispatch("getLyric", this.playList[this.playListIndex].id); // this.updataTime()
  },

  updated() {
    this.$store.dispatch("getLyric", this.playList[this.playListIndex].id);
    this.addDuration();
  },

  methods: {
    play: function () {
      // 判断音乐是否播放
      if (this.$refs.audio.paused) {
        this.$refs.audio.play();
        this.updataIsbtnShow(false); // 播放调用函数传值

        this.updataTime();
      } else {
        this.$refs.audio.pause();
        this.updataIsbtnShow(true); // 暂停就清除定时器

        clearInterval(this.interVal);
      }
    },
    addDuration: function () {
      this.updataDuration(this.$refs.audio.duration);
    },
    updataTime: function () {
      this.interVal = setInterval(() => {
        this.updataCurrentTime(this.$refs.audio.currentTime);
      }, 10);
    },
    // 对 改变播放按钮的图标显示 方法进行解构
    ...mapMutations(['updataIsbtnShow', 'updataDetailShow', 'updataCurrentTime', 'updataDuration'])
  },
  watch: {
    // 监听歌曲下标，如果改变就自动播放
    playListIndex: function () {
      this.$refs.audio.autoplay = true; // 更改图标

      if (this.$refs.audio.paused) {
        this.updataIsbtnShow(false);
      }
    },
    playList: function () {
      if (this.isbtnShow) {
        this.$refs.audio.autoplay = true;
        this.updataIsbtnShow(false);
      }
    }
  }
};